<template>
  <div id="check-in-container">
    <gmap-autocomplete
      style="width:75%"
      @place_changed="setPlace">
    </gmap-autocomplete>
    <b-button v-show="!this.locatingUser" size="sm" class="ml-3" @click="geolocate()">Find Me</b-button>
    <b-button disabled v-show="this.locatingUser" size="sm" class="ml-3"><b-spinner small></b-spinner></b-button>
    <p/>
    <gmap-map
      :center="center"
      :zoom="zoomLevel"
      style="width:100%; height: 400px;">
      <gmap-marker
        :position="center"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

<script>
export default {
  name: 'CheckIn',
  data () {
    return {
      // Example defaults to Montreal
      center: { lat: 45.508, lng: -73.587 },
      zoomLevel: 12,
      currentPlace: null,
      locatingUser: false,
      form: {
        locationSearch: ''
      }
    }
  },
  mounted() {
    this.geolocate();
  },
  methods: {
    setPlace (place) {
      this.currentPlace = place
      this.zoomLevel = 16
      this.center.lat = this.currentPlace.geometry.location.lat()
      this.center.lng = this.currentPlace.geometry.location.lng()
    },
    geolocate () {
      this.locatingUser = true
      navigator.geolocation.getCurrentPosition(position => {
        this.zoomLevel = 12
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }
      this.locatingUser = false
      })
    }
  }
}
</script>

<style scoped>
.form-group {
  text-align: left !important;
}
</style>